ne el nivel requerido de protección de la información y determinando la gestión integral de los datos y su ciclo.")]),_c('p',{staticClass:"mb-5",attrs:{"data-aos":"fade-down"}},[_vm._v("Lo invitamos a ver el siguiente video que presenta el esquema de clasificación de los datos.")]),_vm._m(6),_c('p',{staticClass:"mb-5",attrs:{"data-aos":"fade-down"}},[_vm._v("Sintetizando, una vez dispuesta la primera estandarización de las generalidades se debe implementar la clasificación de los datos con la particularidad de considerar y ponderar los beneficios de la integración de tecnologías emergentes que van surgiendo. Además, se debe tener una fase de monitoreo constante orientada a la supervisión y debe velar por la garantía de calidad de los datos, desencadenado en ejercicios de mejora continua que motiven a un proceso de integridad de la información. Así mismo, como última fase se debe tener en cuenta la revisión y ajuste bajo esquemas de periodicidad con el fin de ponderar la integralidad de todo el sistema de clasificación de los datos y la protección necesaria para la información.")]),_vm._m(7)])],1)
